import React from 'react'

const Card = (props) =>(
    <div className="my-card">
        <div className="my-card__img-box">
            <img src={require('../img/backpack.svg')} alt="bags" className="my-card__img" />
        </div>
        <div className="my-card__content">
            <h2 className="my-card__header m-3">Books and Stationary</h2>
            <p className="my-card__para m-3">Books,instruments and supporting material in one package. Hassle free Processing and exchange.</p>
            <button className="btn btn-success m-3">Know More</button>
        </div>
    </div>

)

export default Card;