import React from 'react';
import NavBar from '../components/NavBar';
import Header from '../components/Header';
import Card from '../components/Card';
import UserCard from '../components/userCard'
const Home = () =>(
    <div class="home-page">
        <NavBar/>
        <Header/>
        <div className="section-why-us" id="feature">
            <h2 className="heading--black">Why Choose us</h2>
            <div className="d-md-flex">
            <Card image={'../img/backpack.png'} header="Books" />
            <Card/>
            </div>
            <div className="d-md-flex">
            <Card/>
            <Card/>
            </div>
        </div>
        <div className="section-testinomial" id="team">
            <div>
                <h2 className="heading--white md--black">
                    What our<br/> customers say
                </h2>
                <ul>
                    <li className="md--black"> 7 institutes</li>
                    <li className="md--black">1000+ Customers</li>
                    <li className="md--black">10000 Books and</li>
                    <li className="md--black">more</li>
                </ul>
            </div>
            <div>
                <UserCard/>
            </div>
            <div>
            <UserCard/>
            </div>
        </div>
        <div className="section-enroll" id="pricing">
            <div className="enroll-card">
                <div className="enroll-card__content">
                    <h2>Ready to get started ?</h2>
                    <p>Join now and be a part of next generation education platform</p>
                </div>
                <div className="enroll-card__btn-box" >
                    <a to="#" className="btn btn-success">Enroll Now</a>
                </div>
            </div>
        </div>
        <footer className="row justify-content-end" id="contact">
            <div className="col">

            </div>
            <div className="col">
                <h3>FEATURE LINK</h3>
                <p>Pricing</p>
                <p>FAQ</p>
                <p>New Registration</p>
                <p>Old Registration</p>

            </div>
            <div className="col">
            <h3>MORE</h3>
            <p>Internships with us</p>
            <p>Campus master</p>
            <p>Career opportunity</p>
            </div>
            <div className="col">
            <h3>COMPANY</h3>
            <p>About</p>
            <p>Team</p>
            <p>Our partners</p>
            <p>Contact Us</p>

            </div>

        </footer>
        <footer>
            <span>Powered</span> By Monktree
        </footer>
    </div>
)

export default Home;