import React from 'react';

const UserCard = () =>(
    <div className="user-card">
        <div className="user-card__img-box">
            <img src={require('../img/profile.jpg')} alt="user" className="user-card__img"/>
        </div>
        <div className="user-card__content">
            <h2>John Doe</h2>
            <p>Some text</p>
        </div>
    </div>
)

export default UserCard;